.container.searches {
    max-width: 1200px;
    margin: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-flow: column;
    padding: 0 20px;
}

.table-group {
    width: 100%;

    @media (max-width: 990px) {
        & {
            overflow-y: auto;
            overflow-x: scroll;
            scrollbar-color: #596E0D #fff;
            scrollbar-width: auto;
        }

        &::-webkit-scrollbar {
            height: 10px;
            background: rgb(138, 201, 138);
            ;
        }

        &::-webkit-scrollbar-track {
            background: white;
        }

        &::-webkit-scrollbar-thumb {
            background: #596E0D;
            border-radius: 30px;
        }
    }
}

.search-and-pagination {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    margin: 20px 0;

    @media (max-width: 990px) {
        & {
            justify-content: center;
            flex-direction: column;
        }

        .search {
            margin: 0px 0 20px;

        }
    }

    .search {
        select {
            margin-left: 10px;
            background-color: #FBF9F4;
            border: 1px solid #57571c;
            color: black;
            cursor: pointer;
            border-radius: 3px;
            padding: 8px 0px 8px 5px;

        }

        input {
            line-height: 1.8rem;
        }
    }

    .pagination {
        display: flex;
        align-items: center;
        justify-content: space-around;

        @media (max-width: 990px) {
            & {
                flex-direction: column;
            }

            .pagination-page {
                margin-bottom: 10px;
            }

            span {
                margin: 20px;

            }

            .buttons {
                margin-left: 0px;

                button {
                    margin-left: 0px;
                }
            }
        }

        input {
            margin-left: 10px;
            background-color: #FBF9F4;
            border: 1px solid #57571c;
            color: black;
            cursor: pointer;
            border-radius: 3px;
            padding: 8px 0px 8px 5px;
        }

        span {
            margin: 10px;
        }

        .buttons {
            display: flex;
            align-items: center;
            justify-content: space-between;
            width: 250px;

            button {
                background-color: #FBF9F4;
                border: 1px solid #57571c;
                color: black;
                cursor: pointer;
                border-radius: 3px;
                padding: 8px 12px;

                &:disabled,
                &[disabled] {
                    background-color: transparent;
                    cursor: auto;
                    color: rgb(163, 163, 163);
                    border: 1px solid #d5d58e;
                }
            }
        }
    }
}

table {
    font-family: 'Roboto', sans-serif;
    border-collapse: collapse;
    width: 100%;
    // margin: 0 auto;
}

table td,
table th {
    border: 1px solid #92AC20;
    padding: 12px 5px;
    font-size: .9rem;
    text-align: center;
    line-height: 1.2rem;
}

table tr:nth-child(even) {
    background-color: rgb(239, 239, 239);
}

table tr:hover {
    background-color: #f5f5dc81;
}

table th,
tfoot td {
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: center;
    background-color: #F5F5DC;
    color: #596E0D;
    font-weight: bold;
}

span svg {
    margin-left: 10px;


    fill: #718231;

    font-size: 0.7rem;
}